import React from 'react';
import PropTypes from 'prop-types';

import {
  BriefModalContainer,
  BriefModalContents,
  BriefModalTitle,
  BriefModalForm,
  BriefModalFormRight,
  BriefModalFormLeft,
  SubmitButton,
} from './styled';

import FormField from './FormField';

const BriefModal = ({ isOpen, onSubmit, onClose }) => isOpen &&
  <BriefModalContainer
    onClick={onClose}
  >
    <BriefModalContents
      onClick={(ev) => ev.stopPropagation()}
    >
      <BriefModalTitle>
               !
      </BriefModalTitle>
      <BriefModalForm onSubmit={onSubmit}>
        <BriefModalFormLeft>
          <FormField
            title=", "
            name="fullname"
            placeholder=""
            required
          />
          <FormField
            title=" E-mail"
            name="email"
            placeholder="email@example.com"
            required
          />
          <FormField
            title=" "
            name="phone"
            placeholder="+7 (XXX) XXX XX XX"
            required
          />
          <FormField
            select
            selectOptions={['   ']}
            title="    ?"
            name="needs"
          />
          <FormField
            title=" "
            name="homepage"
            placeholder="example.com"
          />
        </BriefModalFormLeft>
        <BriefModalFormRight>
          <FormField
            textArea
            title=" "
            name="additionalInfo"
            placeholder="( )"
            rows={23}
          />
          <SubmitButton> </SubmitButton>
        </BriefModalFormRight>
      </BriefModalForm>
    </BriefModalContents>
  </BriefModalContainer>;

BriefModal.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  onSubmit: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired,
};

export default BriefModal;



